import axios from "axios";
import React from "react";
import { editor, components } from "../rete";

export default function Header() {
  const saveHandler = async (el) => {
    el.preventDefault();

    try {
      const res = await axios.post(
        !process.env.NODE_ENV || process.env.NODE_ENV === "development"
          ? "/json/api/set.php?id=1"
          : "https://demo142.grissoft.com.ua/json/api/set.php?id=1",
        JSON.stringify(editor.toJSON())
      );
    } catch (e) {
      console.log(e);
    } finally {
    }
  };

  return (
    <header className="header">
      <div className="logo">
        <img src="./img/logo.svg" />
      </div>
      <button className="icon-btn icon-btn_save" onClick={saveHandler} />
    </header>
  );
}
